"use client";

import React, { useState } from "react";
import NavBar from "../../components/NavBar";
import MovieCard from "../../components/MovieCard";

export default function SearchPage() {
    const [query, setQuery] = useState("");
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(false);

    const handleSearch = async () => {
        if (!query) return;
        setLoading(true);

        // Simulated API call for movies
        setTimeout(() => {
            const results = [
                { id: 1, title: "Inception", rating: 8.8, genre: "Sci-Fi" },
                { id: 2, title: "The Dark Knight", rating: 9.0, genre: "Action" },
                { id: 3, title: "Interstellar", rating: 8.6, genre: "Adventure" },
            ].filter((movie) => movie.title.toLowerCase().includes(query.toLowerCase()));

            setMovies(results);
            setLoading(false);
        }, 1000);
    };

    return (
        <>
            {/* Navigation Bar */}
            <NavBar />

            {/* Page Content */}
            <div className="p-6 bg-black min-h-screen">
                <h1 className="text-4xl font-bold text-red-500 mb-6">Search Movies</h1>
                <div className="flex items-center gap-4 mb-6">
                    <input
                        type="text"
                        placeholder="Search for a movie..."
                        value={query}
                        onChange={(e) => setQuery(e.target.value)}
                        className="flex-1 px-4 py-2 rounded bg-gray-800 text-white focus:outline-none focus:ring-2 focus:ring-red-500"
                    />
                    <button
                        onClick={handleSearch}
                        className="bg-red-500 text-white px-6 py-2 rounded hover:bg-red-600 transition-colors"
                    >
                        Search
                    </button>
                </div>

                {loading ? (
                    <p className="text-gray-200 text-lg">Loading...</p>
                ) : movies.length > 0 ? (
                    <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
                        {movies.map((movie) => (
                            <MovieCard
                                key={movie.id}
                                title={movie.title}
                                rating={movie.rating}
                                genre={movie.genre}
                                onDetailsClick={() => alert(`Details of ${movie.title}`)}
                            />
                        ))}
                    </div>
                ) : (
                    <p className="text-gray-400 text-lg">No results found.</p>
                )}
            </div>
        </>
    );
}
